messageInBackground();

export function messageInBackground() {
  chrome.alarms.onAlarm.addListener((alarm) => {
    if (alarm.name === 'testAlarm') {
      chrome.notifications.create('test', {
        type: 'basic',
        iconUrl: 'img/icon-128.png',
        title: 'Test Message',
        message: 'You are awesome!',
        priority: 2,
      });
    }
  });
}
